<template>
  <div class="clock" @click="toggleClock">
    <span v-if="mini" class="clock--mini" >
      {{ remainingTime | clock }}
    </span>
    <dl v-else class="clock--big">
      <dt> 剩余时间 </dt>
      <dd> {{ remainingTime | clock}} </dd>
      <dt> 总题数 </dt>
      <dd> {{ totalQuestionsCnt }} </dd>
      <dt> 剩余题数 </dt>
      <dd> {{ restQuestionsCnt }} </dd>
    </dl>
  </div>
</template>

<script>
export default {
  props: {
    totalTime: {
      type: Number,
    },
    totalQuestionsCnt: {
      type: Number,
    },
    restQuestionsCnt: {
      type: Number,
    },
  },
  data() {
    return {
      remainingTime: 0,
      handler: null,

      mini: true,

      beginTime: 0,
    };
  },
  mounted() {
    let beginTime = +localStorage.getItem('begin-time');
    let remainingTime = +localStorage.getItem('remaining-time');

    if (beginTime) {
      this.beginTime = beginTime;
    } else {
      this.beginTime = Date.now();
      localStorage.setItem('begin-time', this.beginTime);
    }

    if (remainingTime) {
      this.remainingTime = remainingTime;
    } else {
      this.remainingTime = this.totalTime;
    }

    const unit = 1000;
    const fn = () => {
      this.remainingTime -= unit;
      if (this.remainingTime <= 0) {
        clearTimeout(this.handler);
        this.handler = null;
        localStorage.removeItem('remaining-time');
        this.$emit('timeout', Date.now() - this.beginTime);
      } else {
        localStorage.setItem('remaining-time', this.remainingTime);
        this.handler = setTimeout(fn, unit);
      }
    };

    setTimeout(fn, unit);
    this.$bus.$on('testing-get-time', this.getTime)
  },
  destroyed() {
    this.$bus.$off('testing-get-time', this.getTime)
    clearTimeout(this.handler);
    localStorage.removeItem('begin-time');
    localStorage.removeItem('remaining-time');
  },
  methods: {
    getTime() {
      this.$emit('get-time', Date.now() - this.beginTime);
    },
    toggleClock() {
      if (this.mini) {
        this.mini = false;
      } else {
        document.getElementsByClassName('clock--big')[0].classList.add('slideOut');
        setTimeout(() => {
          this.mini = true;
        }, 300);
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.clock {
  position: fixed;
  color: #777;
  right: 0px;
  top: 10em;
  width: 50px;
  padding: 0.4em 0.6em;

  .clock--big {
    margin: 0px;
    position: relative;
    background: white;
    animation: slideIn 0.3s;
    border: 1px solid silver;
    padding-left: 0.6em;
    width: 200px;
    height: 200px;
    right: 150px;
  }

  .clock--mini {
    border: 1px solid silver;
    padding: 0.4em 0.6em;
    border-radius: 2px;
  }
}

@keyframes slideIn {
  from {
    right: -100px;
    width: 0px;
  }
  to {
    right: 150px;
    width: 200px;
  }
}
.slideOut {
  animation: slideOut 0.3s !important;
}

@keyframes slideOut {
  from {
    right: 150px;
    width: 200px;
  }
  to {
    right: -100px;
    width: 0px;
  }
}

</style>
