<template>
  <ul class="flex-column">
    <li class="option" v-for="(option, index) in data.options" :key="option">
      <checkbox
      :id="'option' + index"
      :checkbox-class="'mc-option'"
      :message="option"
      :checked="reply && reply.length > index ? reply[index] : false"
      />
    </li>
  </ul>
</template>

<script>
export default {
  props: {
    data: {
      type: Object,
    },
    reply: {
    },
  },
  mounted() {
    this.$bus.$on('testing-next', this.complete);
  },
  destroyed() {
    this.$bus.$off('testing-next', this.complete);
  },
  methods: {
    complete(data) {
      const reply = [];
      const els = document.getElementsByClassName('mc-option');
      for (let i = 0; i < els.length; i += 1) {
        if (els[i].checked) {
          reply.push(true);
        } else {
          reply.push(false);
        }
      }
      this.$emit('complete', {
        reply,
        index: data.index,
        submit: data.submit,
      });
    },
  },
};
</script>