<template>
    <ul>
      <li
        v-for="i in data.answer.length"
        :key="i"
        class="mt-1"
      >
      <label for="">{{ i }}. </label>
        <input 
        :value="(reply && reply[i - 1]) ? reply[i - 1] : ''"
          class="pure-input replys" type="text" />
      </li>
    </ul>
</template>

<script>
export default {
  props: {
    data: {
      type: Object,
    },
    reply: {
      type: String,
    },
  },
  mounted() {
    this.$bus.$on('testing-next', this.complete);
  },
  destroyed() {
    this.$bus.$off('testing-next', this.complete);
  },
  methods: {
    complete(data) {
      const reply = [];
      const els = document.getElementsByClassName('replys');
      for (let i = 0; i < els.length; i += 1) {
        reply.push(els[i].value.trim());
      }
      this.$emit('complete', {
        reply: reply,
        index: data.index,
        submit: data.submit,
      });
    },
  },
}
</script>