<template>
  <ul class="single-choice-options">
    <li 
    v-for="(option, index) in data.options" 
    :key="option"
    class="mt-1"
    >
      <radio
      :id="'option' + index"
      :message="option"
      name="single-choice-options"
      @change="changeOption($event, index)"
      :checked="localReply === index"
      />
    </li>
  </ul>
</template>

<script>
export default {
  props: {
    data: {
      type: Object,
      required: true,
    },
    reply: {
      default: -1,
    },
  },
  data() {
    return {
      localReply: this.reply,
    };
  },
  mounted() {
    this.$bus.$on('testing-next', this.submit);
  },
  destroyed() {
    this.$bus.$off('testing-next', this.submit);
  },
  methods: {
    changeOption(event, index) {
      if (event.target.checked) {
        this.localReply = index;
      }
    },
    submit(data) {
      this.$emit('complete', {
        reply: this.localReply,
        index: data.index,
        submit: data.submit,
      });
    },
  },
};
</script>
