<template>
  <div class="text-sm text-light flex-column flex-center">
    <div class="flex-item-center">
      <div v-if="settings.schedule && !counting">
        <p>
          测试将于 {{ `${settings.date} : ${settings.time}` }} 开放.
        </p>
      </div>
      <div v-else>
        <p>
          一共 {{ length }} 题
        </p>
        <p v-if="settings.timerMode === $constants.EACH">
          每题时间限制: 
          {{ `${settings.timeLimit.minutes ? settings.timeLimit.minutes +  '分' : ''}` }}
          {{ `${settings.timeLimit.seconds ? settings.timeLimit.seconds + ' 秒' : ''}` }}
        </p>
        <p v-else-if="settings.timerMode === $constants.ALL">
          时间限制: 
          {{ `${settings.timeLimit.minutes ? settings.timeLimit.minutes + '分' : ''}` }}
          {{ `${settings.timeLimit.seconds ? settings.timeLimit.seconds + '秒' : ''}` }}
        </p>
        <p>
          测试会在倒计时后开始
        </p>
        <p>
          倒计时: {{ remainingTime | clock }}
        </p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    settings: {
      type: Object,
      required: true,
    },
    length: {
      type: Number,
    },
  },
  data() {
    return {
      remainingTime: 3000,
      counting: false,

      countdownHandler: null,
    };
  },
  // watch: {
  //   remainingTime(value) {
  //     if (value <= 0) {
  //       clearInterval(this.countdownHandler);
  //       this.$emit('start');
  //     }
  //   },
  // },
  mounted() {
    if (this.settings.schedule) {
      if (Date.now() > this.settings.scheduleAt) {
        this.countdown();
      }
    } else {
      this.countdown();
    }
  },
  methods: {
    countdown() {
      this.counting = true;
      const unit = 1000;
      this.countdownHandler = setInterval(() => {
        this.remainingTime -= unit;
        console.log(this.remainingTime);
        if (this.remainingTime <= 0) {
          clearInterval(this.countdownHandler);
          this.$emit('start');
        }
      }, unit);
    },
  },
};
</script>