    MultipleChoice,
    BlankFilling,
  },
  props: {
    question: {
      type: Object,
      required: true,
    },
    index: {
      type: Number,
      required: true,
    },
    reply: {
    },
  },
  methods: {
    complete(data) {
      this.$emit('complete', data);
    },
  },
};
</script>

