<template>
  <div class="testing-container flex-column pd-1" v-if="!session.loading">
    <div class="flex-1 flex-column">
      <before-test
      class="flex-1"
      v-if="session.preparing"
      :settings="session.test.settings"
      :length="session.test.questions.length"
      @start="start"
      />
      <after-test
      :id="session.result._id"
      v-else-if="session.end"
      />
      <div v-else>
        <div class="flex-row">
          <span @click="exit" class="ml-auto exit">
            <i @click="exit" class="fas fa-door-closed"></i>
            Exit
          </span>
        </div>
        <question-card
        class="mt-1"
        :question="session.test.questions[session.currentIndex]"
        :index="session.currentIndex + 1"
        :reply="session.replys[session.currentIndex]"
        @complete='toComplete'
        />
        <div class="flex-row mt-1">
          <button v-if="session.test.settings.timerMode === $constants.ALL" class="pure-button" @click="next(session.currentIndex - 1)">prev</button>
          <button class="pure-button" @click="next(session.currentIndex + 1)">next</button>
          <button
            class="ml-auto pure-button pure-button-primary"
            @click.prevent="toSubmit">提交</button>
        </div>
        <footer v-if="session.test.settings.timerMode === $constants.ALL" class="flex-row mt-1">
          <ul class="pager flex-1">
            <li
            v-for="index in session.test.questions.length"
            class="pager-index"
            :id="'pager-' + index"
            :class="{active: session.currentIndex + 1 === index, 'done': session.status[index - 1]}"
            :key="index"
            @click="next(index - 1)"
            >
              <span>{{ index }}</span>
            </li>
          </ul>
        </footer>
      </div>
    </div>
    <timer
    v-if="!session.preparing && !session.end"
    :total-time="session.remainningTime"
    :total-questions-cnt="session.test.questions.length"
    :rest-questions-cnt="session.remainningQuestions"
    @timeout="toTimeout"
    @get-time="getTime"
    />
  </div>
</template>

<script>
import BeforeTest from './components/before-test.vue';
import AfterTest from './components/after-test.vue';
import Timer from './components/timer.vue';
import QuestionCard from './components/question-card.vue';

export default {
  components: {
    BeforeTest,
    AfterTest,
    Timer,
    QuestionCard,
  },
  props: {
    title: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      session: {
        result: {},
        loading: true,
        preparing: true,

        end: false,
        test: {},
        remainningTime: 0,
        remainningQuestions: 0,
        currentIndex: 0,

        replys: [],
        status: [],

        flag: false,
        timeUsed: 0,
        dataCollected: false,
      },
    };
  },
  mounted() {
    let session = localStorage.getItem('testing-session');
    if (session) {
      this.session = JSON.parse(session);
    } else {
      this.$axios
        .get(`${this.$url.tests}/${this.title}`)
        .then((resp) => {
          this.session.test = resp.data;
          this.session.loading = false;
          this.resetTimer();
        })
        .catch(() => {
          this.session.loading = false;
        });
    }
  },

  methods: {
    start() {
      this.session.preparing = false;
      this.resetTimer();
    },
    next(index) {
      if (index < 0) {
        return;
      }
      if (index >= this.session.test.questions.length) {
        return;
      } else {
        this.$bus.$emit('testing-next', {
          index,
          submit: false,
        });
      }
    },
    toTimeout() {
      if (this.session.test.settings.timerMode === this.$constants.EACH) {
        if (this.session.currentIndex === this.session.test.questions.length - 1) {
          return;
        } else {
          this.session.currentIndex += 1;
          this.refreshCache();
          this.resetTimer();
        }
      } else if (this.session.test.settings.timerMode === this.$constants.ALL) {
        this.toSubmit();
      }
    },
    refreshCache() {
      localStorage.setItem('testing-session', JSON.stringify(this.session));
    },
    cleanCache() {
      localStorage.removeItem('testing-session');
    },
    resetTimer() {
      const { minutes, seconds } = this.session.test.settings.timeLimit;
      this.session.remainningTime = (minutes * 60 * 1000) + (seconds * 1000);
    },
    toComplete(data) {
      this.session.replys[this.session.currentIndex] = data.reply;
      switch(this.session.test.questions[this.session.currentIndex].type) {
        case this.QUESTION_TYPE.SINGLE_CHOICE: {
          if (data.reply > -1) {
            this.session.status[this.session.currentIndex] = true; 
          }
          break;
        }
        case this.QUESTION_TYPE.MULTIPLE_CHOICE: {
          if (data.reply.some((flag) => flag)) {
            this.session.status[this.session.currentIndex] = true; 
          }
          break;
        }
        case this.QUESTION_TYPE.BLANK_FILLING: {
          if (data.reply.some((value) => value.length > 0)) {
            this.session.status[this.session.currentIndex] = true; 
          }
          break;
        }
      }
      this.session.remainningQuestions = this.session.test.questions.length - this.session.status.reduce((acc, cur) => {
        if (cur) {
          return acc + 1;
        } else {
          return acc;
        }
      }, 0);
      if (data.submit) {
        this.session.dataCollected = true;
        if (this.session.timeUsed !== 0) {
          this.submit();
        }
      } else {
        this.session.currentIndex = data.index;
        this.refreshCache();
        this.resetTimer();
      }
    },
    getTime(data) {
      this.session.timeUsed = data;
      if (this.session.dataCollected) {
        this.submit();
      }
    },
    toSubmit() {
      this.$bus.$emit('testing-get-time');
      this.$bus.$emit('testing-next', {
        index: -1,
        submit: true,
      });
    },
    formatResult() {
      const user = window.$user;
      const score = this.session.test.questions.reduce((acc, cur, index) => {
        let ok = false;
        console.log(this);
        if (cur.type === this.QUESTION_TYPE.MULTIPLE_CHOICE) {
          if (this.session.replys[index] && this.session.replys[index].join('') === cur.answers.join('')) {
            ok = true;
          }
        }
        if (cur.type === this.QUESTION_TYPE.BLANK_FILLING) {
          if (this.session.replys[index] && this.session.replys[index].join('') === cur.answer.join('')) {
            ok = true;
          }
        }
        if (cur.type === this.QUESTION_TYPE.SINGLE_CHOICE) {
          if (this.session.replys[index] !== -1 && this.session.replys[index] === cur.answer) {
            ok = true;
          }
        }
        if (ok) {
          // eslint-disable-next-line
          acc += +cur.score;
        }
        return acc;
        }, 0);
        const details =  this.session.test.questions.map((q, index) => {
          if (q.type === this.QUESTION_TYPE.MULTIPLE_CHOICE) {
            if (!this.session.replys[index]) {
              return {
                question: q,
                reply: [],
                verdict: 1,
              }
            } else {
              return {
                question: q,
                reply: this.session.replys[index],
                verdict: this.session.replys[index].join('') === q.answers.join('') ? 0 : 1,
              };
            }
          } else if (q.type === this.QUESTION_TYPE.SINGLE_CHOICE) {
            return {
              question: q,
              reply: this.session.replys[index],
              verdict: this.session.replys[index] === q.answer ? 0 : 1,
            };
          } else  if (q.type === this.QUESTION_TYPE.BLANK_FILLING) {
            if (!this.session.replys[index]) {
              return {
                question: q,
                reply: this.session.replys[index],
                verdict: 1,
              }
            } else {
              return {
                question: q,
                reply: this.session.replys[index],
                verdict: this.session.replys[index].join('') === q.answer.join('') ? 0 : 1,
              };
            }
          }
          return {
            question: q,
            reply: null,
            verdict: false,
          };
        });
      const result = {
        title: this.session.test.title,
        username: user.username,
        score: score,
        details: details
      };
      return result;
    },
    submit() {
      if (this.session.flag) {
        return;
      }
      this.cleanCache();
      const result = this.formatResult();
      result.createdAt = Date.now();
      let timeLimit = (+this.session.test.settings.timeLimit.minutes * 60 + +this.session.test.settings.timeLimit.seconds) * 1000;
      result.timeUsed = this.session.timeUsed;
      result.timeLimit = timeLimit;
      this.session.flag = true;
      this.$axios
        .post(`${this.$url.results}`, result)
        .then((resp) => {
          this.session.result = resp.data;
          this.session.flag = false;
          this.session.end = true;
        })
        .catch((err) => {
          this.session.flag = false;
          this.$alert('error', err.message);
        });
    },
    exit() {
      this.$router.replace('/');
    },
    confirm() {
      return window.confirm('进度不会保存, 确定要离开吗?');
    },
  },
  destroyed() {
    this.cleanCache();
  },
  beforeRouteLeave(to, from, next) {
    if (this.confirm()) {
      this.cleanCache();
      this.$bus.$emit('changeMode', 'normal');
      window.$mode = 'normal';
      next();
    } else {
      next(false);
    }
  },
};
</script>

